/**
 * 
 * @export
 * @param {*} value
 * @param {*} callback
 */
export function validateBarcode(value, callback) {
    if (!value) {
        callback(new Error(''))
    } else if (!Number.isInteger(value)) {
        callback(new Error(''))
    } else {
        callback()
    }
}

/**
 * 
 * @export
 * @param {*} value
 * @param {*} callback
 */
export function validatePrice(value, callback) {
    if (!value) {
        callback(new Error(''))
    } else if (!Number.isInteger(value)) {
        callback(new Error(''))
    } else {
        callback()
    }
}

/**
 * 
 * @export
 * @param {*} value
 * @param {*} callback
 */
export function validateSpecs(value, callback) {
    if (value && value != "") {
        callback()
    } else {
        callback(new Error(''))
    }
}


/**
 * 
 * @export
 * @param {*} value
 * @param {*} callback
 * @param {*} message
 */
export function isEmpty(value, callback, message) {
    if (value && value != "") {
        callback()
    } else {
        callback(new Error(message))
    }
}