/**
 * 
 * @export
 * @param {*} status
 * @returns
 */
export function getSaleStatus(status) {
    if (status === 1) {
        return ""
    } else if (status === 2) {
        return ""
    } else if (status === 3) {
        return ""
    } else {
        return ""
    }
}

