import {
  get,
  post
} from "@/utils/request";

/**
 * 
 * @export
 * @param {*} options
 * @returns
 */
export function getStudentList(options) {
  return get("/user/getStudentList", options)
}

/**
 * 
 * @export
 * @param {*} data
 * @returns
 */
export function updateStudentInfo(data) {
  return post(data)
}

/**
 * 
 * @export
 * @param {*} data
 * @returns
 */
export function deleteStudent(data) {
  return post(data)
}

export function onCharge(data) {
  return post("/admin/charge", data)
}